import React, { useState } from 'react'
import {
    motion,
    useScroll,
    useTransform,
    useMotionTemplate,
    useMotionValueEvent,
  } from "framer-motion";

const SmallScreenPackage = ({name, price, img, col, current}) => {
    const [expand, setExpand] = useState({
        one: false,
        two: false,
        three: false,
    })
    const {scrollY} = useScroll()
    useMotionValueEvent(scrollY, "change", (latest) => {
        console.log(latest);
      });
      const current2 = parseInt(current)
    const x = useTransform(scrollY, [current2, current2+170, current+370], [0, 150, 0])
  return (
    <motion.div className='w-screen h-[fit] relative '>
        <motion.div
        style={{
            x
        }}
        className={`h-[127px] w-[320px] ${col} rounded-r-full mb-10 relative left-[-150px] flex justify-center  font-junge text-black flex-col `}>
            <h1 className='pl-[50px]'>{name}</h1>
            <p className='pl-[50px]'>{price}</p>
        </motion.div>
        <motion.img 
        style={{
            x
        }}
        className = 'h-[127px] absolute top-0 left-[50px] ' src={img} alt="" />
    </motion.div>
  )
}

export default SmallScreenPackage