import React from "react";
import { useState } from "react";
import { motion } from "framer-motion";
import icon from "../../utility/logo2.png";
import icon2 from "../../utility/socialmedia1.png";
import icon3 from "../../utility/socialmedia2.png";
import icon4 from "../../utility/socialmedia3.png";
import Moveavableicon from "../../utils/moveavableicon";
import { hover } from "@testing-library/user-event/dist/hover";
const About = () => {
  const [hovered, setHovered] = useState({
    contact: false,
    learnmore: false,
  });

  return (
    <motion.div className="h-[100vh] w-screen text-white flex flex-col justify-center md:gap-10 gap-3 md:ml-20 ml-10 relative font-jetBrains">
      <h1 className="font-jetBrains md:text-5xl text-3xl md:w-[600px] w-[200px]">
        Ewperience Cullinary Excellence with Us
      </h1>
      <p className="md:w-screen w-[200px]">
        Creating unforgettable dining experiences tailored to your unique events
        and preferences.
      </p>
      <div className="flex gap-10">
        <div className="h-[50px] w-[200px] overflow-hidden rounded-lg bg-white">
          <motion.div
            className="bg-black h-[200px] w-[190px] "
            initial={{
              height: "0px",
              width: "0px",
              borderTopRightRadius: "0px",
              borderBottomRightRadius: "0px",
            }}
            animate={{
              height: hovered.contact ? "800px" : "0px",
              width: hovered.contact ? "200px" : "0px",
              borderTopRightRadius: hovered.contact ? "0%" : "100%",
              borderBottomRightRadius: hovered.contact ? "0%" : "100%",
              transition: {
                // Optional delay
                duration: 1, // Animation duration
                ease: "easeInOut", // Smooth easing
              },
            }}
            onMouseEnter={() => {
              setHovered((prev) => ({
                ...prev, // Preserve other properties of `prev`
                contact: true, // Update the `contact` property
              }));
            }}
            onMouseLeave={() => {
              setHovered((prev) => ({
                ...prev,
                contact: false,
              }));
            }}
          >
            <button
              style={{
                color: hovered.contact ? "white" : "black",
           
                
              }}
              className="bg-transparent rounded-lg md:w-[200px] w-[150px] h-[50px] flex justify-evenly items-center relative border-white border-[2px]"
            >
              <div className="absolute left-0">
                <Moveavableicon>
                  <li></li>
                </Moveavableicon>
              </div>
              Contact US
            </button>
          </motion.div>
        </div>

        <div className="h-[50px] w-[200px] overflow-hidden rounded-lg">
          <motion.div
            className="bg-white h-[200px] w-[190px] "
            initial={{
              height: "0px",
              width: "0px",
              borderTopRightRadius: "0px",
              borderBottomRightRadius: "0px",
            }}
            animate={{
              height: hovered.learnmore ? "800px" : "0px",
              width: hovered.learnmore ? "200px" : "0px",
              borderTopRightRadius: hovered.learnmore ? "0%" : "100%",
              borderBottomRightRadius: hovered.learnmore ? "0%" : "100%",
              transition: {
                // Optional delay
                duration: 1, // Animation duration
                ease: "easeInOut", // Smooth easing
              },
            }}
            onMouseEnter={() => {
              setHovered((prev) => ({
                ...prev,
                learnmore: true,
              }));
            }}
            onMouseLeave={() => {
              setHovered((prev) => ({
                ...prev,
                learnmore: false,
              }));
            }}
          >
            <button
              style={{
                color: hovered.learnmore ? "black" : "white",
                
              }}
              className="bg-transparent  rounded-lg border-white border-[2px] md:w-[200px] w-[150px] h-[50px] flex justify-evenly items-center relative"
            >
              <div className="absolute left-0">
                <Moveavableicon>
                  <li></li>
                </Moveavableicon>
              </div>
              Learn More
            </button>
          </motion.div>
        </div>
      </div>
      <img className="w-[80px]" src={icon} alt="" />
      <span className="border-b-white border-[1px] w-[90%] md:mt-[100px] mt-[50px]"></span>

      <div className="bg-white h-[50px] md:w-[200px] w-[100px] rounded-full md:absolute md:right-[200px]  md:bottom-[20px] flex justify-evenly items-center">
        <img className="md:w-[40px] h-fit w-[20px]" src={icon2} alt="" />
        <img className="md:w-[40px] h-fit w-[20px]" src={icon3} alt="" />
        <img className="md:w-[40px] h-fit w-[20px]" src={icon4} alt="" />
      </div>
    </motion.div>
  );
};

export default About;
