import React, { useEffect, useState } from "react";
import { useRef } from "react";
import { ReactLenis, useLenis } from "lenis/react";
import {
  motion,
  useScroll,
  useTransform,
  useMotionTemplate,
  useMotionValueEvent,
  useElementScroll,
} from "framer-motion";
import { div } from "motion/react-client";
import Lenis from "lenis";
import Naivadyam1 from "../../utility/packages/Naivadyam-1.jpg";
import Naivadyam2 from "../../utility/packages/Naivadyam-2.jpg";
import Bhojyam1 from "../../utility/packages/Bhojyam-1.jpg";
import Bhojyam2 from "../../utility/packages/Bhojyam-2.jpg";
import P1 from "../../utility/packages/p-1.jpg";
import P2 from "../../utility/packages/p2.jpg";

import HeadName from "./HeadName";
import PackageDetails from "./PackageDetails";
const NewPackages = () => {
  const [isSmallScreen, setIsSmallScreen] = useState(false);
  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth <= 740) {
        setIsSmallScreen(true); // Small screen (sm)
      } else {
        setIsSmallScreen(false); // Larger screens (md and up)
      }
    };

    handleResize(); // Check on initial render
    window.addEventListener("resize", handleResize);

    return () => window.removeEventListener("resize", handleResize);
  }, []);
  const SECTION_HEIGHT = isSmallScreen ? 500 : 1500;

  const desc = "This package offers a balanced selection of classic Indian flavors with a mix of sweets, snacks, curries, and traditional staples. It's a simpler, yet satisfying spread that includes essentials like dal,sambar, rice, and curd, complemented by sweet treats, pickles,papad, and fresh salads. A wholesome choice for those seeking afamiliar, comfort-food experience"
  const desc1= "An expanded offering with a welcoming touch, including a welcome drink, snack, and sweet to start the experience. With additional options in sweets, sprouts, and chaat, this package provides varietywhile preserving the flavors of traditional Indian dishes. Ideal forgatherings where variety and hospitality are a priority."
  const desc2 = " This package delivers a rich and diverse experience with enhanced   options across snacks, sweets, curries, and salads. It features specialty items like Chinese dishes and a variety of chaat, making it perfect for guests who enjoy a wider range of cuisines andflavors. The addition of a fruit stall and sweet paan offers aunique and refreshing conclusion to the meal."
  const NaivedyamAr = ['Small Events',
    'Budget Friendly',
    'Indian Cuseines',
    'Comfortable and Simple' ]
  return (
    <ReactLenis root>
      <div className=" w-screen">
        <Heading SECTION_HEIGHT={SECTION_HEIGHT} />
        <HeadName CurrentPoint={0.4*SECTION_HEIGHT} SECTION_HEIGHT={SECTION_HEIGHT} img1={Naivadyam1} img2={Naivadyam2} disc={' Planning on a small occasion ?'}/>
      
      <PackageDetails name='Naivadyam' disc={desc} CurrentPoint={`${1.1 * SECTION_HEIGHT } `} isSmallScreen={isSmallScreen} SECTION_HEIGHT={SECTION_HEIGHT} arr= {NaivedyamAr}/> 
 
      <HeadName CurrentPoint={`${1.1 * SECTION_HEIGHT + SECTION_HEIGHT } `} SECTION_HEIGHT={SECTION_HEIGHT} img1={Bhojyam1} img2={Bhojyam2} disc={' Something Big And Good Hospitality?'}/>
       <PackageDetails name='Bhojyam' disc={desc1} CurrentPoint={`${1.8 * SECTION_HEIGHT + SECTION_HEIGHT } `} isSmallScreen={isSmallScreen} SECTION_HEIGHT={SECTION_HEIGHT} arr= {NaivedyamAr}/> 
       <HeadName CurrentPoint={`${1.8 * SECTION_HEIGHT + 2*SECTION_HEIGHT } `} SECTION_HEIGHT={SECTION_HEIGHT} img1={P1} img2={P2} disc={'Also want international Cusines?'}/>
       <PackageDetails name='PanchaBakysam' disc={desc2} CurrentPoint={`${2.5 * SECTION_HEIGHT + 2*SECTION_HEIGHT } `} isSmallScreen={isSmallScreen} SECTION_HEIGHT={SECTION_HEIGHT} arr= {NaivedyamAr}/> 
       
      <div className="w-screem h-screen bg-black"></div>
      </div>
    </ReactLenis>
  );
};
const Heading = ({ SECTION_HEIGHT }) => {
  const ref = useRef(null);
  const { scrollYProgress } = useScroll({
    target: ref,
    offset: ["end start", "start end"],
  });
  const {scrollY} = useScroll()

  const y = useTransform(scrollY, [0, SECTION_HEIGHT-10], [600, 0]);
  const opacity = useTransform(scrollY, [SECTION_HEIGHT-10, SECTION_HEIGHT], [1, 0]);
  const scale = useTransform(scrollY, [ 0, SECTION_HEIGHT], [1.5, 1]);

  return (
    <motion.div
      className="w-screen bg-black overflow-x-hidden "
      style={{
        height: `calc(${0.4*SECTION_HEIGHT }px  + 100vh)`,
      }}
    >
      <motion.div
        // ref= {ref}
        style={{}}
        className="w-full h-[100vh] sticky top-10 "
      >
        <motion.h1
          ref={ref}
          style={{
            y,
            opacity,
            scale,
          }}
          className="md:text-4xl text-[10px] text-white text-center"
        >
          LET ME TAKE YOU THROUGH THE PACKAGES
        </motion.h1>
      </motion.div>
    </motion.div>
  );
};










export default NewPackages;
