import React from "react";
import {
  motion,
  useScroll,
  useTransform,
  useMotionTemplate,
  useMotionValueEvent,
  useElementScroll,
} from "framer-motion";
const HeadName = ({ CurrentPoint, img1, img2, disc, SECTION_HEIGHT }) => {
  const scrollYW = window.scrollY;
  const { scrollY } = useScroll();
  console.log(scrollY.get());
  useMotionValueEvent(scrollY, "change", (latest) => {
    console.log(latest);
  });
  const currentPointer = parseInt(CurrentPoint)
  const leftStyle = useTransform(
    scrollY,
    [currentPointer , currentPointer  + (0.5*SECTION_HEIGHT)],
    [-600, 0]
  );
  const rightStyle = useTransform(
    scrollY,
    [currentPointer  , currentPointer + (0.5*SECTION_HEIGHT)],
    [600, 0]
  );
  const opacity = useTransform(scrollY, [currentPointer , currentPointer  + (0.8 *SECTION_HEIGHT )], [0, 1])
  const y= useTransform(scrollY, [ CurrentPoint, CurrentPoint + (0.8 *SECTION_HEIGHT )], [0, 0])
  return (
    <div
      className="bg-black w-screen relative" 
      style={{
        height: `calc(${0.7 * SECTION_HEIGHT}px`,
      }}
    >
      <div className="flex justify-evenly pt-10 overflow-x-hidden gap-3 sticky top-0">
        <motion.img
          style={{
            x: leftStyle,
          }}
          className="md:w-[500px]  h-fit relative w-[60%] rounded-md "
          src={img1}
          alt=""
        />
        <motion.img
          style={{
            x: rightStyle,
          }}
          className="md:w-[500px] h-fit relative w-[70%] rounded-md"
          src={img2}
          alt=""
        />
      </div>
      <div className=" overflow-y-hidden">
      <motion.div
      style={{
        opacity,
        // y
      }}
        className="bg-white md:h-[500px] md:w-[500px] absolute md:left-[calc(50%-250px)] rounded-3xl left-[calc(50%-125px)] h-[250px] w-[250px] text-black
        flex justify-center md:text-4xl text-lg font-jetBrains items-center text-center top-[30%]
        "
        // style={{ y, opacity }}
      >
        {disc}
      </motion.div>
      </div>
     
    </div>
  );
};

export default HeadName;
